import React from "react";
import { useState, useEffect } from "react";
import {useDispatch, useSelector} from 'react-redux';
import { getAllDogs } from "../Redux/Actions";
import { Link } from "react-router-dom";
import DogCard from './DogCard'
import Paginado from "./Paginado";
import s from '../Styles/home.module.css'

export default function Home(){

const dispatch = useDispatch()
const allDogs = useSelector((state) => state.dogs)

const [currentPage,setCurrentPage] = useState(1) 
const [dogsPerPage,setDogsPerPage] = useState(8) 
const indexOfLastDog = currentPage * dogsPerPage  
const indexOfFirstDog = indexOfLastDog - dogsPerPage 
const currentDogs = allDogs.slice(indexOfFirstDog,indexOfLastDog)

const paginado = (pageNumbers) => {
    setCurrentPage(pageNumbers)
}

useEffect(()=> {
    dispatch(getAllDogs());
},[dispatch])

function handleClick(e){
e.preventDefault();
dispatch(getAllDogs());
}

return (
<div className={s.rootDiv}>

    <Link to='/dogs'> Crear Perro </Link>
    <h1>DOGS APP</h1>
    <button onClick={e=>{handleClick(e)}}>
        Volver a cargar todas las razas
    </button>
   
    <div>
        
        <span>Ordenar x Nombre</span>
        <select>
            <option value='asc'>Ascendente</option>
            <option value='dec'>Descendente</option>
        </select>
        <span>Ordenar x Peso</span>
        <select>
            <option value='asc'>Ascendente</option>
            <option value='dec'>Descendente</option>
        </select>
        
        <select>
            <option value = 'all'>Todas las Razas</option>
            <option value = 'created'>Creadas</option>
            <option value = 'api'>Existentes</option>
        </select>

        <Paginado
            dogsPerPage = {dogsPerPage}
            allDogs = {allDogs.lenght}
            paginado = {paginado}
        />
    
    {
      currentDogs?.map( e => {
        return (
            <div>
                <Link to ={'/home'}>
                <DogCard name={e.name} image={e.image} temperament={e.temperament} weight={[e.weight.imperial, e.weight.metric]}/>
                </Link>
        </div>
        )
      })
      
    }
    
    </div>

</div>
)

}
