 import React from "react";
 import s from '../Styles/dogCard.module.css'

 export default function DogCard({image, name, temperament, weight}) {
    return (
        <div className={s.dogCardContainer}>
            <h3>{name}</h3>
            <h3>{temperament}</h3>
            <h4>{weight}</h4>
            <img src={image} alt="img not found" width='350px' height='250px' />
        </div>
    )
 }
